import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./components/App";
import Root from "./root";
import { BrowserRouter } from "react-router-dom";
ReactDOM.render(
  <Root>
    <BrowserRouter>
      <App />
    </BrowserRouter>
  </Root>,
  document.getElementById("root")
);

